import React from 'react';
import { Route, Routes } from 'react-router-dom';

const BusinessDetails = React.lazy(() => import('./BusinessDetails/BusinessDetails'));
const BusinessAddress = React.lazy(() => import('./BusinessAddress/BusinessAddress'));
const Review = React.lazy(() => import('./Review/Review'));
const KybApproved = React.lazy(() => import('./KybApproved/KybApproved'));
const KybDeclined = React.lazy(() => import('./KybDeclined/KybDeclined'));
const Ownership = React.lazy(() => import('./YourOwnerShip/YourOwnerShip'));
const OwnersList = React.lazy(() => import('./AddOwners/AddOwners'));
const OwnerDetails = React.lazy(() => import('./PersonalDetails/PersonalDetails'));
const OwnerAddress = React.lazy(() => import('./PersonalAddress/PersonalAddress'));
const TheirOwnerShip = React.lazy(() => import('./TheirOwnerShip/TheirOwnerShip'));

function KybRouter() {
  return (
    <Routes>
      <Route path="businessDetails" element={<BusinessDetails />} />
      <Route path="businessAddress" element={<BusinessAddress />} />
      <Route path="review" element={<Review />} />
      <Route path="approved" element={<KybApproved />} />
      <Route path="declined" element={<KybDeclined />} />
      <Route path="ownership" element={<Ownership />} />
      <Route path="ownersList" element={<OwnersList />} />
      <Route path="ownerDetails" element={<OwnerDetails />} />
      <Route path="ownerAddress" element={<OwnerAddress />} />
      <Route path="ownerOwnership" element={<TheirOwnerShip />} />
    </Routes>
  );
}

export default KybRouter;
